				this.stock.text = name;
			},

    		addObject: function(){

    			let o = this;
    			let data = new FormData();

    			data.append("name", this.name);
				data.append("svg", this.svg);
    			data.append("content", this.content);
    			data.append("slug", this.slug);
    			data.append("seo_title", this.seo_title);
    			data.append("seo_desc", this.seo_desc);
				data.append("stocksID", o.stock.object);
				data.append("parent", this.parent.object);

    			axios.post(o.$store.state.domain + window.location.pathname, data).then(function(response){

    				if(response.data.response){
    					o.statusm          = 1;
    					o.message          = "–î–æ–±–∞–≤–ª–µ–Ω!";
    				}else{
    					o.statusm 	= -1;
    					o.message	= response.data.err;
    				}

    			}).catch(function(error){
                    console.log(error);
    				o.statusm 	= -1;
    				o.message	= "–ù–µ–∏–∑–≤–µ—Å—Ç–Ω–∞—è –æ—à–∏–±–∫–∞!";
    			})

    		}

    	}

    }
</script>
