<template>
    <div>
        <h2>–î–æ–±–∞–≤–ª–µ–Ω–∏–µ –¶–≤–µ—Ç–∞</h2>

        <form class="form" action="." method="post" accept-charset="utf-8">
            <div id="tab" class="demo">

                <label for='name'>–ù–∞–∏–º–µ–Ω–æ–≤–∞–Ω–∏–µ</label>
    			<input v-model="name" id='name' class="form-control" name='name' type='text'/>

				<label for='code'>–ö–æ–¥</label>
				<input v-model="code" id='code' class="form-control" name='code' type='text'/>

				<label for='poster'>–§–æ—Ç–æ —Ü–≤–µ—Ç–∞</label>
				<div class="inputPoster">
					<input ref="poster" @change="posterFiles" type="file" name="poster" accept="image/jpeg,image/png" hidden />
					<div class="image-name">{{ poster_name }}</div>
					<div class="select-image" @click="$refs.poster.click()">–í—ã–±—Ä–∞—Ç—å</div>
				</div>

    			<div v-if="statusm == 0" class="message">{{ message }}</div>
    			<div v-else-if="statusm == -1" class="message alert-danger">{{ message }}</div>
    			<div v-else="statusm == 1" class="message alert-success">{{ message }}</div>

    			<button type="submit" class="btn btn-primary sub" v-on:click.prevent="addObject">–î–æ–±–∞–≤–∏—Ç—å</button>
            </div>
        </form>
    </div>
</template>


<script>
    export default {
        data: function(){
    		return {

                name: 		"",
				code: 		"",

				posterFile:	"",
				poster_name: "–Ω–µ –≤—ã–±—Ä–∞–Ω...",

        		statusm: 	0,
        		message:	"",
    		}
    	},

        methods: {

			posterFiles(){
                this.poster_name = this.$refs.poster.files[0].name;
    			this.posterFile = this.$refs.poster.files[0];
    		},

    		addObject: function(){

    			let o    = this;
    			let data = new FormData();

	    		data.append("name", o.name);
				data.append("code", o.code);
				data.append("poster", o.posterFile);

    			axios.post(o.$store.state.domain + window.location.pathname, data, {
    				headers: {'Content-Type': 'multipart/form-data'}
			    }).then(function(response){
    				if(response.data.response){
    					o.statusm 	= 1;
    					o.message	= "–î–æ–±–∞–≤–ª–µ–Ω!";
    				}else{
    					o.statusm 	= -1;
    					o.message	= response.data.err;
    				}

    			}).catch(function (error){
    				o.statusm 	= -1;
    				o.message	= "–ù–µ–∏–∑–≤–µ—Å—Ç–Ω–∞—è –æ—à–∏–±–∫–∞!";
    			})

    		}

    	}

    }
</script>
